//apenas inicia la app se ejecuta la funci√≥n iniciarApp
document.addEventListener('DOMContentLoaded',function(){
    iniciarApp();
});

//esta funcion ejecuta la funcion crear galeria
function iniciarApp(){
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija(){
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body')

    window.addEventListener('scroll',function(){
         //esta funcion te retorna la ubicacion del elemento en determinado momento
        if( sobreFestival.getBoundingClientRect().top < 0){ //si el scroll pasa por la seccion sobre festival fija el header
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        }else{
            barra.classList.remove('fijo'); //sino lo quita
            body.classList.remove('body-scroll');
        }
    });
}

function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a');

    enlaces.forEach(enlace => {
        enlace.addEventListener('click',function(e){
            e.preventDefault();
            const seccion = e.target.attributes.href.value; //lee a que le dimos click y regresa el valor del href
            const seccionScroll = document.querySelector(seccion);
            seccionScroll.scrollIntoView({behavior: "smooth"});
        })
    });
}

function crearGaleria(){
    //seleccionamos el section de galeria imagenes
    const galeria = document.querySelector('.galeria-imagenes');
    
    //se hace uso del for para recorrer cada imagen de la carpeta 
    for(let i=1;i<=12;i++){
        //se crea el elemento picture
        const imagen = document.createElement('picture');

        //se inyecta el script html que crea cada imagen
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="Imagen galeria">
        `
        //cada vez que se cliquea una imagen se ejecuta la funcion mostrarImagen que recibe de parametro el numero de imagen que se selecciono
        imagen.onclick = function(){
            mostrarImagen(i);
        }

        //muestra en el div de galeria las imagenes
        galeria.appendChild(imagen);

    }

}

//recibe por parametro el numero de la imagen del ciclo for
function mostrarImagen(numeroImagen){

    //se crea el elemento de la pura imagen
    const imagen = document.createElement('picture');

    //se le asigna la imagen dependiendo del indice a el elemento de imagen creado arriba
        imagen.innerHTML = `
            <source srcset="build/img/grande/${numeroImagen}.avif" type="image/avif">
            <source srcset="build/img/grande/${numeroImagen}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/grande/${numeroImagen}.jpg" alt="Imagen galeria">
        `
        //creamos el overlay donde mostrar la imagen
        const overlay = document.createElement('DIV');
        overlay.appendChild(imagen);
        overlay.classList.add('overlay');
        overlay.onclick = function(){
            const body = document.querySelector('body');
            body.classList.remove('body-imagen-fijar');
            overlay.remove();
        }

        //boton para cerrar el overlay
        const cerrarImagen = document.createElement('P');
        cerrarImagen.textContent = 'X';
        cerrarImagen.classList.add('btn-cerrar');

        cerrarImagen.onclick = function(){
            //usamos el selector para que despues de cerrar la imagen le quite el estilo de bloquear el scroll
            const body = document.querySelector('body');
            body.classList.remove('body-imagen-fijar');
            overlay.remove();
        }
        overlay.appendChild(cerrarImagen);

        //mostramos el overlay que creamos ya cargado con la imagen en el body
        const body = document.querySelector('body');
        body.appendChild(overlay);
        body.classList.add('body-imagen-fijar');
}